.home-services {
  padding-top: 110px;
  padding-bottom: 75px;
  height: 100%;
  min-height: 500px;
  background-image: linear-gradient(0.87deg, rgba(2, 55, 135, 0.9) 0.69%, rgba(81, 38, 174, 0.4736) 100.48%, rgba(38, 92, 174, 0.0102906) 100.49%, rgba(195, 195, 196, 0.00520833) 100.5%, #76A6EF 100.51%), url("../img/home-services.webp");
  background-position-x: center;
  background-position-y: 0;
  background-size: cover;
  background-repeat: no-repeat;

  &__wrap-text {
    padding-top: 40px;
    padding-bottom: 80px;
    width: 100%;
    max-width: 780px;
  }

  &__title {
    font-weight: 700;
    font-size: calc(50px + (10 + 10 * 0.7) * ((100vw - 320px)/ 1440)); // 50 60px;
    line-height: calc(75px + (7 + 7 * 0.7) * ((100vw - 320px)/ 1440)); // 75 82px;
    color: $light1;
  }

  &__description {
    font-weight: 400;
    font-style: normal;
    font-size: 29px;
    line-height: 40px;
    color: $light1;
  }

  &__form {
    display: grid;
    align-content: center;
    justify-content: center;
    grid-template-columns: repeat(5, 200px);
    grid-template-rows: repeat(2, 60px);
    width: 100%;
    min-height: 175px;
    background-color: $light1;
    gap: 15px;
  }

  &__sort-item {
    border: 1px solid $dark-color1;
    border-radius: 5px;
    padding: 10px;
    font-weight: 600;
    font-style: normal;
    font-size: 18px;
    line-height: 27px;
  }

  &__sort-item:nth-child(2) {
    grid-column: 2/6;
  }

  &__sort-item:last-child {
    background-color: $main-color;
    transition: 0.2s;
  }

  &__sort-item:last-child:hover {
    transform: scaleY(1.05) translateY(-2px);
    transition: transform 0.2s;
  }

  &__option {
    width: 100px;
  }

  &__option:nth-child(odd) {
    background-color: $main-color;
  }
}

.no-webp .home-services {
  background-image: linear-gradient(0.87deg, rgba(2, 55, 135, 0.9) 0.69%, rgba(81, 38, 174, 0.4736) 100.48%, rgba(38, 92, 174, 0.0102906) 100.49%, rgba(195, 195, 196, 0.00520833) 100.5%, #76A6EF 100.51%), url("../img/home-services.jpg");
}

@media screen and (max-width: 1260px) {
  .home-services {
    &__form {
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, 60px);
      padding: 20px;
    }

    &__sort-item:nth-child(2) {
      grid-column: 1/4;
      grid-row: 1/2;
    }
  }
}

@media screen and (max-width: 780px) {
  .home-services {
    &__form {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(4, 60px);
      padding: 20px;
    }

    &__sort-item:nth-child(2) {
      grid-column: 1/3;
    }
  }
}
@media screen and (max-width: 530px) {
  .home-services {
    &__form {
      grid-template-columns: repeat(1, 1fr);
      grid-template-rows: repeat(7, 60px);
      padding: 20px;
    }

    &__sort-item:nth-child(2) {
      grid-column: 1/2;
    }
  }
}