.footer {
  font-family: "Roboto";
  background-color: $secondary-color;

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__list-services {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 45px 0;
  }

  &__list-services::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: $light1;
    opacity: 0.2;
  }

  &__item-services {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    padding: 0 10px;
    min-height: 48px;
  }

  &__item-services::before {
    content: "";
    position: absolute;
    left: -100%;
    bottom: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 2px;
    background-color: $main-color;
  }

  &__item-services::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -100%;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    background-color: $main-color;
    opacity: 0.7;
  }

  &__item-services:hover:before {
    animation: leftToCenter 0.3s;
    animation-fill-mode: forwards;
  }

  &__item-services:hover:after {
    animation: bottomToCenter 0.3s;
    animation-delay: 0.3s;
    animation-fill-mode: forwards;
  }

  &__item-services:hover &__link-services {
    color: $light1;
  }

  &__link-services {
    display: inline-block;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: $light2;
    transition: color 0.5s;
  }

  &__locations-list {
    position: relative;
    display: flex;
    padding: 35px 0;
    width: 100%;
  }

  &__locations-list::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 2px;
    background-color: $light1;
    opacity: 0.2;
  }

  &__location-item {
    display: flex;
    flex-direction: column;
    width: 100%;
    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
    color: $light1;
  }

  &__country-list {
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    color: $light2;
  }

  &__country-item {
    padding-top: 25px;
  }

  &__town-list {
    display: flex;
    flex-wrap: wrap;
    padding-top: 10px;
  }

  &__town-item {
    position: relative;
    z-index: 1;
    display: flex;
    overflow: hidden;
    min-height: 48px;
  }

  &__town-item::before {
    content: "";
    position: absolute;
    left: -100%;
    bottom: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 2px;
    background-color: $main-color;
    transition: left 0.3s;
  }

  &__town-item::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -100%;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    background-color: $main-color;
    opacity: 0.7;
    transition: bottom 0.3s;
  }

  &__town-item:hover:before {
    animation: leftToCenter 0.3s;
    animation-fill-mode: forwards;
  }

  &__town-item:hover:after {
    animation: bottomToCenter 0.3s;
    animation-delay: 0.3s;
    animation-fill-mode: forwards;
  }

  &__town-item:not(:last-child) {
    margin-right: 30px;
  }

  &__town-link {
    padding: 0 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 48px;
    color: $light2;
  }

  &__list {
    display: grid;
    column-gap: 45px;
    grid-template-columns: repeat(6, auto);
    grid-template-rows: 48px;
    padding: 45px 0;
  }

  &__logo-link {
    display: block;
    margin: 0 auto;
    width: 110px;
    height: 100%;
    background-image: url("../img/logo.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__small-text {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
    color: $light2;
  }

  &__application-link {
    display: block;
    height: 100%;
    min-width: 140px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    background-color: transparent;
  }

  &__google {
    background-image: url("../img/applicationGP.webp");
  }

  &__appstore {
    background-image: url("../img/applicationAP.webp");
  }
}

.no-webp .footer {
  &__google {
    background-image: url("../img/applicationGP.png");
  }

  &__appstore {
    background-image: url("../img/applicationAP.png");
  }
}

@media screen and (max-width: 1080px) {
  .footer {
    &__list {
      grid-template-columns: repeat(3, auto);
      grid-template-rows: 48px 48px;
      row-gap: 25px;
    }

    &__item:first-child,
    &__item:last-child,
    &__item:nth-child(5) {
      grid-row: 1/2;
    }

    &__application-link {
      grid-row: 1/2;
    }
  }
}

@media screen and (max-width: 990px) {
  .footer {
    &__list-services {
      display: grid;
      grid-template-columns: repeat(4, auto);
      grid-template-rows: repeat(2, auto);
      row-gap: 10px;
    }
  }
}

@media screen and (max-width: 650px) {
  .footer {
    &__list-services {
      grid-template-columns: repeat(3, auto);
      grid-template-rows: repeat(3, auto);
    }

    &__list {
      grid-template-columns: repeat(2, auto);
      grid-template-rows: repeat(3, 48px);
      row-gap: 25px;
    }

    &__item:first-child {
      grid-column: 1/2;
      grid-row: 1/2;
    }

    &__item:last-child {
      grid-column: 1/2;
      grid-row: 2/3;
    }

    &__item:nth-child(3) {
      grid-row: 2/3;
    }

    &__item:nth-child(4) {
      grid-column: 2/3;
      grid-row: 1/2;
    }

    &__item:nth-child(5) {
      grid-column: 1/2;
      grid-row: 3/4;
    }
  }
}

@media screen and (max-width: 560px) {
  .footer {
    &__country-item {
      position: relative;
      overflow: hidden;
      padding-left: 10px;
      height: 60px;
    }

    &__country-item::before {
      content: "";
      position: absolute;
      left: 0;
      top: 10px;
      display: block;
      border: 1px solid #fff;
      width: 100%;
      height: 48px;
    }

    &__country-item::after {
      content: "";
      position: absolute;
      right: 15px;
      top: 12px;
      display: block;
      margin: 15px auto;
      border: 15px solid;
      border-color: $light2 transparent $light2 transparent;
      border-bottom: 0;
      width: 0;
      height: 0;
      transition: border 0.3s;
    }

    &__town-list {
      flex-direction: column;
      margin-left: 15px;
      padding-top: 15px;
    }

    &__town-item {
      border: 1px solid #fff;
      padding: 7px 20px;
      width: 100%;
      height: max-content;
    }
  }

  .hiden {
    height: max-content;
  }

  .hiden::after {
    content: "";
    position: absolute;
    right: 15px;
    top: 12px;
    display: block;
    margin: 15px auto;
    border: 15px solid;
    border-color: $light2 transparent $light2 transparent;
    border-top: 0;
    width: 0;
    height: 0;
    transition: border 0.3s;
  }
}

@media screen and (max-width: 470px) {
  .footer {
    &__list-services {
      grid-template-columns: repeat(2, auto);
      grid-template-rows: repeat(4, auto);
    }
  }
}