.nav-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;

  &__home-link {
    display: block;
    width: 110px;
    height: 24px;
  }

  &__svg {
    display: block;
    width: 100%;
    height: 100%;
  }

  &__list-items {
    display: flex;
  }

  &__item:not(:last-child) {
    margin-right: 40px;
  }

  &__item {
    position: relative;
    z-index: 2;
    display: flex;
    overflow: hidden;
    height: 48px;
  }

  &__item::before {
    content: "";
    position: absolute;
    left: -100%;
    bottom: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 2px;
    background-color: $main-color;
    transition: left 0.3s;
  }

  &__item::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -100%;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    background-color: $main-color;
    opacity: 0.7;
    transition: bottom 0.3s;
  }

  &__item:hover:before {
    animation: leftToCenter 0.3s;
    animation-fill-mode: forwards;
  }

  &__item:hover:after {
    animation: bottomToCenter 0.3s;
    animation-delay: 0.3s;
    animation-fill-mode: forwards;
  }

  &__item:hover &__item-link {
    color: $dark-color1;
    transition: color 0.5s;
  }

  &__item-link {
    display: inline-block;
    padding: 0 10px;
    font-size: 18px;
    line-height: 48px;
    color: $light1;
    transition: color 0.5s;
  }
}

@media screen and (max-width: 810px) {
  .nav-menu {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;

    &__item:not(:last-child) {
      margin-right: 30px;
    }
  }
}

@media screen and (max-width: 660px) {
  .nav-menu {
    position: fixed;
    left: 200%;
    top: 0;
    bottom: auto;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: auto;
    padding-top: 80px;
    width: 100%;
    background-color: $dark-color1;
    transition: left 0.4s;

    &_active {
      left: 0;
      display: flex;
      transition: left 0.4s;
    }

    &__home-link {
      position: absolute;
      top: 30px;
      width: calc(94% - 160px);
      height: 40px;
    }

    &__list-items {
      flex-direction: column;
      align-items: center;
      justify-content: space-evenly;
      padding-bottom: 40px;
      height: 80%;
    }

    &__item {
      margin-right: 0;
      min-height: 48px;
      text-align: center;
    }

    &__item:not(:last-child) {
      margin-right: 0;
    }

    &__item-link {
      padding: 10px 0;
      font-weight: 700;
      font-size: 30px;
      line-height: 1.5;
    }
  }
}