.services-items {
  &__container {
    position: relative;
    padding-top: 75px;
    padding-bottom: 55px;
  }

  &__list-items {
    display: flex;
    justify-content: space-between;
  }

  &__list-item {
    display: flex;
    flex-direction: column;
    width: 32%;
    transition: box-shadow 0.3s;
  }

  &__list-item:hover {
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    transition: box-shadow 0.3s;
  }

  &__list-item:hover &__link {
    background-color: $main-color;
    transition: background-color 0.3s;
  }

  &__list-item:hover &__wrap-arrow {
    background-color: #fff;
    transition: background-color 0.3s;
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    padding: 45px 25px;
    width: 100%;
    height: 135px;
    background-color: #fff;
    transition: background-color 0.3s;
  }

  &__illustration {
    width: 100%;
    height: auto;
    object-fit: cover;
  }

  &__wrap-arrow {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 38px;
    height: 38px;
    box-shadow: 0px 11px 11px -5px rgba(34, 60, 80, 0.6);
    background-color: $main-color;
    transition: background-color 0.3s;
  }

  &__arrow {
    width: 10px;
    height: 12px;
  }

  &__text {
    margin-left: 25px;
    font-weight: 700;
    font-style: normal;
    font-size: 14px;
    line-height: 19px;
    color: $dark-color2;
  }

  &__arrowSw {
    display: none;
  }
}

@media screen and (max-width: 960px) {
  .services-items {
    &__container {
      overflow: hidden;
    }

    &__list-item {
      flex-shrink: 0;
      width: 49%;
    }

    &__link {
      background-color: $main-color;
      transition: background-color 0.3s;
    }

    &__arrowSw {
      display: initial;
    }

    &__wrap-arrow {
      background-color: #fff;
      transition: background-color 0.3s;
    }
  }
}