.book-number {
  &__wrapper {
    padding: 100px 11vw;
    background-color: $detachment;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__description {
    margin-bottom: 20px;
  }

  &__form {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 700px;
  }

  &__number {
    border: 1px solid dark-color2;
    padding-left: 74px;
    width: 100%;
    height: 78px;
    font-weight: 400;
    font-style: normal;
    font-size: 29px;
    line-height: 40px;
    color: $dark-color1;
    background-color: $light1;
  }

  &__number:focus {
    color: $dark-color2;
  }

  &__btn-send {
    position: absolute;
    right: 12px;
    padding: 10px 40px;
    font-weight: 400;
    font-style: normal;
    font-size: 29px;
    line-height: 40px;
    color: $dark-color2;
    background-color: $main-color;
    transition: transform 0.3s;
  }

  &__btn-send:hover {
    transform: scale(1.1);
    transition: transform 0.3s;
  }

  &__flag {
    position: absolute;
    left: 12px;
    width: 40px;
    height: 30px;
  }
}

@media screen and (max-width: 720px) {
  .book-number {
    &__wrapper {
      padding: 65px 5vw;
    }
  }
}

@media screen and (max-width: 600px) {
  .book-number {
    &__number {
      padding-left: 47px;
      font-size: 24px;
    }

    &__btn-send {
      right: 7px;
      padding: 10px 25px;
      font-size: 18px;
    }

    &__flag {
      top: 29px;
      width: 30px;
      height: 20px;
    }
  }
}

@media screen and (max-width: 444px) {
  .book-number {
    &__wrapper {
      padding: 45px 11vw;
    }

    &__form {
      flex-direction: column;
    }

    &__number {
      width: 100%;
      font-size: 18px;
    }

    &__btn-send {
      position: relative;
      align-self: flex-end;
      margin-top: 20px;
    }
  }
}