.advantages {
  padding: 55px 0;

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__description {
    display: flex;
    flex-direction: column;
    margin-right: 80px;
    width: 65%;
  }

  &__list {
    display: flex;
    flex-direction: column;
    padding-top: 40px;
  }

  &__item {
    display: grid;
    column-gap: 30px;
    align-items: center;
    grid-template-columns: 92px calc(100% - 92px);
    grid-template-rows: 1fr 1fr;
  }

  &__item:not(:last-child) {
    margin-bottom: 50px;
  }

  &__item-illustration {
    grid-column: 1/2;
    grid-row: 1/3;
    width: 92px;
    height: 92px;
  }

  &__item-title {
    font-weight: 400;
    font-style: normal;
    font-size: 29px;
    line-height: 40px;
  }

  &__text {
    font-weight: 400;
    font-style: normal;
    font-size: 21px;
    line-height: 29px;
  }

  &__illustration {
    position: relative;
    display: block;
    width: 35%;
    min-height: 555px;
    background: url("../img/advantages.webp") 100% no-repeat;
  }

  &__illustration::before {
    content: "";
    position: absolute;
    left: -18%;
    top: 12%;
    display: block;
    width: 18%;
    height: 78%;
    background-color: $main-color;
  }
}

.no-webp .advantages__illustration {
  background: url("../img/advantages.jpg") 100% no-repeat;
}

@media screen and (max-width: 800px) {
  .advantages {
    &__wrapper {
      align-items: center;
    }

    &__illustration {
      height: 300px;
      min-height: 300px;
    }
  }
}

@media screen and (max-width: 680px) {
  .advantages {
    &__wrapper {
      flex-direction: column;
    }

    &__description {
      margin-right: 0;
    }

    &__illustration {
      margin-top: 45px;
      width: 100%;
      max-width: 425px;
      max-height: 555px;
    }

    &__illustration::before {
      left: 42px;
      top: auto;
      bottom: -45px;
      width: 340px;
      height: 45px;
    }
  }
}

@media screen and (max-width: 500px) {
  .advantages {
    &__item {
      grid-template-columns: 1fr;
      grid-template-rows: 92px 1fr 1fr;
      text-align: center;
      row-gap: 15px;
    }

    &__item-illustration {
      grid-column: 1/2;
      grid-row: 1/2;
      margin: 0 auto;
    }

    &__illustration::before {
      left: 8vw;
      bottom: -25px;
      width: 80vw;
      height: 25px;
    }
  }
}