.header {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  height: 110px;
}

.burger {
  position: fixed;
  right: 4%;
  top: 20px;
  z-index: 20;
  display: none;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  background-color: #fc3;
}

.burger__icon {
  position: relative;
  width: 40px;
  height: 5px;
  background-color: #fff;
}

.burger__icon::before {
  content: '';
  position: absolute;
  left: 0;
  top: -13px;
  width: 40px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon::after {
  content: '';
  position: absolute;
  left: 0;
  top: 13px;
  z-index: 30;
  width: 40px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon.burger__icon_active {
  background-color: transparent;
}

.burger__icon.burger__icon_active::before {
  transform: rotate(45deg);
  top: 0;
  background-color: #fff;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.burger__icon.burger__icon_active::after {
  transform: rotate(-45deg);
  top: 0;
  background-color: #fff;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

@media screen and (max-width: 660px) {
  .burger {
    display: flex;
  }
}

.burger_disable {
  display: none;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.nav-menu__home-link {
  display: block;
  width: 110px;
  height: 24px;
}

.nav-menu__svg {
  display: block;
  width: 100%;
  height: 100%;
}

.nav-menu__list-items {
  display: flex;
}

.nav-menu__item:not(:last-child) {
  margin-right: 40px;
}

.nav-menu__item {
  position: relative;
  z-index: 2;
  display: flex;
  overflow: hidden;
  height: 48px;
}

.nav-menu__item::before {
  content: "";
  position: absolute;
  left: -100%;
  bottom: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #fc3;
  transition: left 0.3s;
}

.nav-menu__item::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -100%;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fc3;
  opacity: 0.7;
  transition: bottom 0.3s;
}

.nav-menu__item:hover:before {
  animation: leftToCenter 0.3s;
  animation-fill-mode: forwards;
}

.nav-menu__item:hover:after {
  animation: bottomToCenter 0.3s;
  animation-delay: 0.3s;
  animation-fill-mode: forwards;
}

.nav-menu__item:hover .nav-menu__item-link {
  color: #333;
  transition: color 0.5s;
}

.nav-menu__item-link {
  display: inline-block;
  padding: 0 10px;
  font-size: 18px;
  line-height: 48px;
  color: #fff;
  transition: color 0.5s;
}

@media screen and (max-width: 810px) {
  .nav-menu {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;
  }
  .nav-menu__item:not(:last-child) {
    margin-right: 30px;
  }
}

@media screen and (max-width: 660px) {
  .nav-menu {
    position: fixed;
    left: 200%;
    top: 0;
    bottom: auto;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: auto;
    padding-top: 80px;
    width: 100%;
    background-color: #333;
    transition: left 0.4s;
  }
  .nav-menu_active {
    left: 0;
    display: flex;
    transition: left 0.4s;
  }
  .nav-menu__home-link {
    position: absolute;
    top: 30px;
    width: calc(94% - 160px);
    height: 40px;
  }
  .nav-menu__list-items {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding-bottom: 40px;
    height: 80%;
  }
  .nav-menu__item {
    margin-right: 0;
    min-height: 48px;
    text-align: center;
  }
  .nav-menu__item:not(:last-child) {
    margin-right: 0;
  }
  .nav-menu__item-link {
