  background-position-x: center;
  background-position-y: 0;
  background-size: cover;
  background-repeat: no-repeat;
}

.home-services__wrap-text {
  padding-top: 40px;
  padding-bottom: 80px;
  width: 100%;
  max-width: 780px;
}

.home-services__title {
  font-weight: 700;
  font-size: calc(50px + (10 + 10 * 0.7) * ((100vw - 320px)/ 1440));
  line-height: calc(75px + (7 + 7 * 0.7) * ((100vw - 320px)/ 1440));
