.footer {
  font-family: "Roboto";
  background-color: #036;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
}

.footer__list-services {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 45px 0;
}

.footer__list-services::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #fff;
  opacity: 0.2;
}

.footer__item-services {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 0 10px;
  min-height: 48px;
}

.footer__item-services::before {
  content: "";
  position: absolute;
  left: -100%;
  bottom: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #fc3;
}

.footer__item-services::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -100%;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fc3;
  opacity: 0.7;
}

.footer__item-services:hover:before {
  animation: leftToCenter 0.3s;
  animation-fill-mode: forwards;
}

.footer__item-services:hover:after {
  animation: bottomToCenter 0.3s;
  animation-delay: 0.3s;
  animation-fill-mode: forwards;
}

.footer__item-services:hover .footer__link-services {
  color: #fff;
}

.footer__link-services {
  display: inline-block;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #ccc;
  transition: color 0.5s;
}

.footer__locations-list {
  position: relative;
  display: flex;
  padding: 35px 0;
  width: 100%;
}

.footer__locations-list::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #fff;
  opacity: 0.2;
}

.footer__location-item {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-weight: 600;
  font-size: 24px;
  line-height: 33px;
  color: #fff;
}

.footer__country-list {
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  color: #ccc;
}

.footer__country-item {
  padding-top: 25px;
}

.footer__town-list {
  display: flex;
  flex-wrap: wrap;
  padding-top: 10px;
}

.footer__town-item {
  position: relative;
  z-index: 1;
  display: flex;
  overflow: hidden;
  min-height: 48px;
}

.footer__town-item::before {
  content: "";
  position: absolute;
  left: -100%;
  bottom: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #fc3;
  transition: left 0.3s;
}

.footer__town-item::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -100%;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fc3;
  opacity: 0.7;
  transition: bottom 0.3s;
}

.footer__town-item:hover:before {
  animation: leftToCenter 0.3s;
  animation-fill-mode: forwards;
}

.footer__town-item:hover:after {
  animation: bottomToCenter 0.3s;
  animation-delay: 0.3s;
  animation-fill-mode: forwards;
}

.footer__town-item:not(:last-child) {
  margin-right: 30px;
}

.footer__town-link {
  padding: 0 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 48px;
  color: #ccc;
}

.footer__list {
  display: grid;
  column-gap: 45px;
  grid-template-columns: repeat(6, auto);
  grid-template-rows: 48px;
  padding: 45px 0;
}

.footer__logo-link {
  display: block;
  margin: 0 auto;
  width: 110px;
  height: 100%;
  background-image: url("../img/logo.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.footer__small-text {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  color: #ccc;
}

.footer__application-link {
  display: block;
  height: 100%;
  min-width: 140px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: transparent;
}

.footer__google {
  background-image: url("../img/applicationGP.webp");
}

.footer__appstore {
  background-image: url("../img/applicationAP.webp");
}

.no-webp .footer__google {
  background-image: url("../img/applicationGP.png");
}

.no-webp .footer__appstore {
  background-image: url("../img/applicationAP.png");
}

@media screen and (max-width: 1080px) {
  .footer__list {
    grid-template-columns: repeat(3, auto);
    grid-template-rows: 48px 48px;
    row-gap: 25px;
  }
  .footer__item:first-child, .footer__item:last-child, .footer__item:nth-child(5) {
    grid-row: 1/2;
  }
  .footer__application-link {
    grid-row: 1/2;
  }
}

@media screen and (max-width: 990px) {
  .footer__list-services {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-template-rows: repeat(2, auto);
    row-gap: 10px;
  }
}

@media screen and (max-width: 650px) {
  .footer__list-services {
    grid-template-columns: repeat(3, auto);
    grid-template-rows: repeat(3, auto);
  }
  .footer__list {
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(3, 48px);
    row-gap: 25px;
  }
  .footer__item:first-child {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .footer__item:last-child {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .footer__item:nth-child(3) {
    grid-row: 2/3;
  }
  .footer__item:nth-child(4) {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .footer__item:nth-child(5) {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}

@media screen and (max-width: 560px) {
  .footer__country-item {
    position: relative;
    overflow: hidden;
    padding-left: 10px;
    height: 60px;
  }
  .footer__country-item::before {
    content: "";
    position: absolute;
    left: 0;
    top: 10px;
    display: block;
    border: 1px solid #fff;
    width: 100%;
    height: 48px;
  }
  .footer__country-item::after {
    content: "";
    position: absolute;
    right: 15px;
    top: 12px;
    display: block;
    margin: 15px auto;
    border: 15px solid;
    border-color: #ccc transparent #ccc transparent;
    border-bottom: 0;
    width: 0;
    height: 0;
    transition: border 0.3s;
  }
  .footer__town-list {
    flex-direction: column;
    margin-left: 15px;
    padding-top: 15px;
  }
  .footer__town-item {
    border: 1px solid #fff;
    padding: 7px 20px;
    width: 100%;
    height: max-content;
  }
  .hiden {
    height: max-content;
  }
  .hiden::after {
    content: "";
    position: absolute;
    right: 15px;
    top: 12px;
    display: block;
    margin: 15px auto;
    border: 15px solid;
    border-color: #ccc transparent #ccc transparent;
    border-top: 0;
    width: 0;
    height: 0;
    transition: border 0.3s;
  }
}

@media screen and (max-width: 470px) {
  .footer__list-services {
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(4, auto);
  }
}

