.ideas2 {
  &__wrapper {
    display: grid;
    column-gap: 55px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 48px 1fr 100px;
    border-bottom: 1px solid $gray-color1;
    padding-top: 100px;
    padding-bottom: 60px;
  }

  &__title {
    grid-column: 1/3;
    grid-row: 1/2;
  }

  &__text {
    grid-column: 1/2;
    grid-row: 2/3;
  }

  &__btn {
    grid-column: 1/2;
    grid-row: 3/4;
    margin-top: 30px;
  }

  &__illustration {
    position: relative;
    grid-column: 2/3;
    grid-row: 2/4;
  }

  &__items-illustration {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
  }

  &__illustration-item {
    width: 32%;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__illustration-item:nth-child(3n+1) {
    background-image: url("../img/ideas2/ideas21.webp");
  }

  &__illustration-item:nth-child(3n+2) {
    background-image: url("../img/ideas2/ideas22.webp");
  }

  &__illustration-item:nth-child(3n+3) {
    background-image: url("../img/ideas2/ideas23.webp");
  }

  &__link {
    display: block;
    width: 100%;
    height: 100%;
  }

  &__arrowSw {
    display: none;
  }
}

.no-webp .ideas2 {
  &__illustration-item:nth-child(3n+1) {
    background-image: url("../img/ideas2/ideas21.jpg");
  }

  &__illustration-item:nth-child(3n+2) {
    background-image: url("../img/ideas2/ideas22.jpg");
  }

  &__illustration-item:nth-child(3n+3) {
    background-image: url("../img/ideas2/ideas23.jpg");
  }
}

@media screen and (max-width: 1000px) {
  .ideas2 {
    &__wrapper {
      display: flex;
      flex-direction: column;
    }

    &__items-illustration {
      padding-top: 30px;
      height: 440px;
    }

    &__btn {
      align-self: center;
    }
  }
}

@media screen and (max-width: 555px) {
  .ideas2 {
    &__illustration {
      overflow: hidden;
    }

    &__illustration-item {
      display: flex;
      flex-shrink: 0;
      height: 160vw;
    }

    &__arrowSw {
      display: initial;
    }
  }
}