.title {
  font-weight: 600;
  font-style: normal;
  font-size: calc(28px + (8 + 8 * 0.7) * ((100vw - 320px) / 1440)); // 28 36px;
  line-height: calc(35px + (14 + 14 * 0.7) * ((100vw - 320px) / 1440)); // 35 49px;
  color: $dark-color1;
}

.description {
  font-weight: 400;
  font-style: normal;
  font-size: calc(21px + (8 + 8 * 0.7) * ((100vw - 320px) / 1440)); // 21 29px;
  line-height: calc(30px + (10 + 10 * 0.7) * ((100vw - 320px) / 1440)); // 30 40px;
  color: #646464;
}

.paragraph {
  padding-top: 20px;
  font-weight: 600;
  font-style: normal;
  font-size: 20px;
  line-height: 27px;
  color: $gray-color1;
}

.button {
  padding: 10px 37px;
  width: max-content;
  height: min-content;
  font-weight: 400;
  font-style: normal;
  font-size: 29px;
  line-height: 40px;
  background-color: $main-color;
  transition: transform 0.4s;
}

.button:hover {
  border-radius: 10px;
  transform: scale(1.05) translateY(-4px);
  transition: transform 0.4s;
}