.ideas {
  position: relative;
  padding-top: 180px;

  &__container {
    position: relative;
  }

  &__title {
    position: absolute;
    left: 4%;
    top: 100px;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 30px 0;
  }

  &__item {
    position: relative;
    display: block;
    margin-bottom: 20px;
    width: 20.35vw;
    height: 28.2vw;
    max-width: 293px;
    max-height: 406px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    transition: box-shadow 0.3s;
  }

  &__item:hover {
    box-shadow: 4px 4px 8px 0.01px rgba(34, 60, 80, 0.42);
    transition: box-shadow 0.3s;
  }

  &__item:hover &__item-description {
    visibility: visible;
  }

  &__link {
    display: block;
    width: 100%;
    height: 100%;
  }

  &__item-description {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    width: 100%;
    height: 100%;
    text-align: center;
    background-color: rgba(255, 204, 51, 0.747);
    visibility: hidden;
  }

  &__item-title {
    font-weight: 600;
    font-style: normal;
    font-size: calc(28px + (8 + 8 * 0.7) * ((100vw - 320px) / 1440)); // 28 36px;
    line-height: calc(35px + (14 + 14 * 0.7) * ((100vw - 320px) / 1440)); // 35 49px;
    color: $dark-color1;
  }

  &__link-item {
    margin: 20px;
    border: 1px solid $dark-color1;
    color: $dark-color1;
  }

  &__item:nth-child(8n+1) {
    background-image: url("../img/ideas/ideas1.webp");
  }

  &__item:nth-child(8n+2) {
    background-image: url("../img/ideas/ideas2.webp");
  }

  &__item:nth-child(8n+3) {
    background-image: url("../img/ideas/ideas3.webp");
  }

  &__item:nth-child(8n+4) {
    background-image: url("../img/ideas/ideas4.webp");
  }

  &__item:nth-child(8n+5) {
    background-image: url("../img/ideas/ideas5.webp");
  }

  &__item:nth-child(8n+6) {
    background-image: url("../img/ideas/ideas6.webp");
  }

  &__item:nth-child(8n+7) {
    background-image: url("../img/ideas/ideas7.webp");
  }

  &__item:nth-child(8n+8) {
    background-image: url("../img/ideas/ideas8.webp");
  }

  &__arrowSw {
    display: none;
  }
}

.no-webp .ideas {
  &__item:nth-child(8n+1) {
    background-image: url("../img/ideas/ideas1.jpg");
  }

  &__item:nth-child(8n+2) {
    background-image: url("../img/ideas/ideas2.jpg");
  }

  &__item:nth-child(8n+3) {
    background-image: url("../img/ideas/ideas3.jpg");
  }

  &__item:nth-child(8n+4) {
    background-image: url("../img/ideas/ideas4.jpg");
  }

  &__item:nth-child(8n+5) {
    background-image: url("../img/ideas/ideas5.jpg");
  }

  &__item:nth-child(8n+6) {
    background-image: url("../img/ideas/ideas6.jpg");
  }

  &__item:nth-child(8n+7) {
    background-image: url("../img/ideas/ideas7.jpg");
  }

  &__item:nth-child(8n+8) {
    background-image: url("../img/ideas/ideas8.jpg");
  }
}

@media screen and (max-width: 860px) {
  .ideas {
    &__container {
      display: flex;
      overflow: hidden;
    }

    &__items {
      display: flex;
      flex-wrap: nowrap;
    }

    &__item {
      display: flex;
      flex-shrink: 0;
      margin-bottom: 0;
      height: 45vw;
    }

    &__arrowSw {
      display: initial;
    }
  }
}

@media screen and (max-width: 780px) {
  .ideas {
    &__item {
      width: 49%;
      height: 60vw;
      max-width: initial;
    }
  }
}

@media screen and (max-width: 535px) {
  .ideas {
    &__item {
      height: 100vw;
    }
  }
}

.fromTop {
  animation: 0.3s forwards fromTop;
}
.fromRight {
  animation: 0.3s forwards fromRight;
}
.fromBottom {
  animation: 0.3s forwards fromBottom;
}
.fromLeft {
  animation: 0.3s forwards fromLeft;
}

.toTop {
  animation: 0.3s forwards toTop;
}
.toRight {
  animation: 0.3s forwards toRight;
}
.toBottom {
  animation: 0.3s forwards toBottom;
}
.toLeft {
  animation: 0.3s forwards ToLeft;
}
