@keyframes leftToCenter {
  0% {
    left: -100%;
  }

  100% {
    left: 0;
  }
}

@keyframes bottomToCenter {
  0% {
    bottom: -100%;
  }

  100% {
    bottom: 0;
  }
}

@keyframes rightToCenter {
  0% {
    right: -100%;
    top: 10.5px;
  }

  70% {
    right: 0;
    top: 10.5px;
    height: 27px;
  }

  100% {
    right: 0;
    top: 0;
    height: 100%;
  }
}


@keyframes fromTop {
  0% {
    visibility: hidden;
  }
  1% {
    visibility: visible;
    transform-origin: top center;
    transform: perspective(600px) rotate3d(1, 0, 0, -90deg);
  }
  100% {
    visibility: visible;
    transform-origin: top center;
    transform: perspective(600px) rotate3d(1, 0, 0, 0deg);
  }
}

@keyframes fromRight {
